@import url('https://fonts.googleapis.com/css2?family=Source+Serif+Pro:ital@1&family=Suwannaphum:wght@300&family=Zilla+Slab&display=swap');
.dark {
    --color-primary: #737fec;/*#7773ec;*/
    --color-danger:    #ff7782;
    --color-success:    #41f1b6;
    --color-warning: #ffbb55;
    --color-white: #111113;
    --color-info-dark: #c3cdda;
    --color-info-light: #69707b;
    --color-dark: #edeffd;
    --color-light: rgba(132, 139, 200, 0.18);
    --left-bar-highlighted-color:#909bff13;
    --color-primary-variant: #111e88;
    --color-dark-variant:    #a3bdcc;
    --color-background: #26212a;
    --color-top:var(--color-white);
    --card-border-radius: 2rem;
    --border-radius-1: 0.4rem;
    --border-radius-2: 0.8rem;
    --border-radius-3: 1.2rem;
    --card-padding: 1.8rem;
    --rightbar-color:#120239;
    --menu-hover-background:rgb(112, 63, 218);
    --dropdown-background:rgb(25, 25, 30);
    --dropdown-border: #30363f;
    --padding-1: 1.2rem;
    --box-shadow: .3rem .3rem .7rem var(--color-light);
    --menu-shadow: .2rem .21rem 0.21rem rgb(57, 45, 65,.61);
}
.light{
    --color-primary: rgb(90, 70, 185);/*#7773ec;*/
    --color-danger:    #ff7782;
    --color-success:    #41f1b6;
    --color-warning: #ffbb55;
    --color-white: #ffffff;
    --color-info-dark: #7d8da1;
    --color-info-light: #dce1eb;
    --color-top:var(--color-white);
    --left-bar-highlighted-color:rgba(95, 105, 200, 0.15);
    --color-dark: #363949;
    --color-light: rgba(124, 124, 161, .2);
    --color-primary-variant: #111e88;
    --color-dark-variant:    #677483;
    --color-background: #f6f6f9;
    --card-border-radius: 2rem;
    --border-radius-1: 0.4rem;
    --border-radius-2: 0.8rem;
    --border-radius-3: 1.2rem;
    --card-padding: 1.8rem;
    --rightbar-color:#eae1ff;
    --menu-hover-background:var(--color-primary);
    --dropdown-background:rgb(247, 245, 250);
    --dropdown-border: #e7f2ff;
    --padding-1: 1.2rem;
    --box-shadow: .7rem 1rem .8rem var(--color-light);
    --menu-shadow: 0rem .71rem 1rem rgba(112, 129, 200, .48);
}
*{  margin:0;padding:0; appearance: none;border:0;
    text-decoration: none; box-sizing: border-box; }
body{   font-family: 'Suwannaphum', serif;
    font-size: .9rem; overflow: hidden; 
    background: var(--color-background); background-color: var(--color-background);}
.login_back_container{z-index:151;position:absolute;display:none;height: 100%;width: 100%;place-items: center;background-color: rgba(30,30,30,0.71);top:0;left:0;}

.login_show{display: grid;}
.container { 
    display: flex;width: 100%; margin: 0 auto; gap: 0rem; overflow: hidden;height: 100%;
    grid-template-columns: 14rem auto 23rem; margin-left:4px;
    justify-content: stretch;
}
.btn{
    padding-top: 3px; position: relative; display:flex;
    border:solid 1px var(--color-info-light); overflow: hidden;
    height:2.15rem;width:2.15rem;    border-radius: 5px;justify-content: center;
    color: var(--color-info-dark);   align-items: center;  transition: 
    all .3s ease; background-color: var(--dock-panel-back); 
    box-shadow: var( --shortcuts-btn-shadow);  
  }
  
a {color: var(--color-dark);}
img {display: block; width: 100%;}
h1 {font-weight: 800; font-size: 1.8rem;}
h2 {font-size: 1.6rem;}
h3 {font-size: 1.42rem;}
h4 {font-size: 1.3rem;}
h5 {font-size: 1.2rem;}
h6 {font-size: 1rem;}
.profile-photo { width: 2.8rem;height: 2.8rem; border-radius: 50%; overflow: hidden;}
.text-muted {color: var(--color-info-dark);}
P{color: var(--color-dark-variant);}
b{color: var(--color-dark-variant)}
.primary {color: var(--color-primary);}
.danger {font-weight:1000px; color: var(--color-danger);}
.success {color: var(--color-success);}
.warning {color: var(--color-warning);}
.top {width: 100%;height:1.8rem;z-index: 100;padding:2px 50px;
    background:var(--color-top);display: flex;align-items:center;gap:2rem;
    justify-content: space-between;box-shadow:var(--box-shadow);overflow: hidden;}
.logo {display: flex;  gap: 0.1rem;padding: 0 0.2rem;color:var(--color-dark)}
.logo img {margin-top: .46rem;   width: 1.6rem;  height: 1.6rem; }
.menu-content{display: inline-block;}
.top * {font-family: 'Suwannaphum', serif;}
.top .hamburger{display: none; border: None;
    font-size: 25px;cursor: pointer; color: var(--color-dark);}
.top .right-bar{gap:4rem;display:flex;max-height: 1.6rem;min-width:fit-content;
    justify-content: space-between;}

.top .right-bar .menu{display: flex;  max-height: inherit;
    height:inherit; width: 30rem;   height: 100%; 
    justify-content: space-around;}
.top .right-bar .menu .menu-btn{
    text-align: center;justify-content: space-between;max-height:1.6rem;
    border-radius: 6px;padding:0rem  .41rem;top:0;
    max-width: max-content;transition: .2s;
    font-size: 1rem;font-weight:bold;color:var(--color-dark-variant) }
    .top .right-bar .menu .menu-btn:hover{
   /* border:solid 1px #fff;*/ color:#fff;
    background-color: var(--menu-hover-background);
    font-size: 1.04rem; font-weight: 650px;}
.top  .right-bar .menu .dropdown{
    position:absolute;top:1.8rem;min-width: 12rem;
    background-color: var(--dropdown-background);
    border-bottom-left-radius: 15px;/*border-bottom-right-radius: 5px;*/
    padding: .5rem; display: none;max-height: max-content;z-index: 150;
    box-shadow: var(--menu-shadow); transition: .2s; font-size: .85rem;}

.top  .right-bar .menu .dropdown p{color:white;}
.top .right-bar .menu .dropdown .dropdown-item{display: flex;position: relative;
    border-bottom-left-radius: 10px;padding:.08rem .5rem;justify-content: space-between;
    border-top: solid 1px var(--dropdown-border);}
.top .right-bar .menu .dropdown .dropdown-item:hover{color:#ffffff;background:var(--menu-hover-background);}

 .sub-drop   {    
    display: none;top:-3vh; padding: .5rem;min-width: 10rem;position:absolute;
    border-bottom-left-radius: 15px;background-color: var(--dropdown-background);
    left:100%; box-shadow: var(--menu-shadow); transition: .2s; font-size: 1rem;}
.sub-dropdown-container{position:relative;}
.sub-dropdown-container:hover > .sub-drop {display: block;}
.top .right-bar .UserBtn{  top:0; min-width:1.6rem;
    min-height:1.6rem;padding: 0;  margin:0; border-radius: 50%;}
.user-logo{position:absolute;top:-4px;padding:0;font-size: 1.6rem;max-height:1.6rem;      transition: all .3s ease ;color:var(--color-dark)}
.top .right-bar .menu .opened {display: block;}



.aside{height:calc(100vh - 1.8rem);max-width: max-content;overflow: hidden;min-width:max-content;transition: all .3s ease; /*background-color: var(--color-white);*/}
.aside *{ font-family: 'Suwannaphum', serif;}
.aside .hamburger-right{
    display: flex; justify-content: left;        
    margin-left: 1.4rem;  color: var(--color-info-dark);
    align-items: center;  transition: all .3s ease; 
   position: relative;padding-right:1.4rem;
}
.aside .hamburger-right span{
    cursor: pointer; color: var(--color-dark);height: 1.9rem;
    font-size: 1.4rem; transition: all .3s ease ;
}
.aside .sidebar { background:var(--color-white); display: block;width: max-content;transition: all .3s ease ;
    flex-direction: column;  height: inherit; position: relative; overflow: scroll;
}
.aside .sidebar::-webkit-scrollbar { width: 0 !important }
.aside ::-webkit-scrollbar { width: 0 !important }
.aside h6 {font-weight: 700;color: var(--color-info-dark);}
.aside .sidebar a{
    display: flex; gap: 1rem; justify-content: left;        
    margin-left: .8rem;  color: var(--color-info-dark);
    align-items: center;  transition: all .3s ease; 
    height: 2.5rem;position: relative;padding-right:.8rem;}
    .aside .sidebar a > h6{transition: all .3s ease; }
.aside .sidebar  span{
    font-size: 1.4rem;  position:relative; top:3px;    transition: all .3s ease ;}
.blank{min-height:4rem;}
.aside .sidebar a.active {
    background:var(--left-bar-highlighted-color);
    color: var(--color-primary);
    margin-left: 0;}
    .aside .sidebar a.active>h6{color:var(--color-primary)}   
.aside .sidebar a.active:before {
    content: "";  width: 4px;  height: 100%;
    background: var(--color-primary);}
.aside .sidebar a.active span { 
    color: var(--color-primary); }
.aside .sidebar a:hover>h6 { color: var(--color-primary);}
.aside .sidebar a:hover { color: var(--color-primary);}


/* #check1{display:none;} */

 .shrink {max-width:40px;min-width:40px;}

.shrink .sidebar a {margin-left:.51rem;gap:.8rem}

.shrink .sidebar a.active {gap:.5rem}
.shrink .sidebar a.active:before {width:2px;gap:.22rem}
.shrink .hamburger-right{margin-left:.6rem;} 
.Main { 
    width:100%;position:relative;padding:0;height: 100%;
    display:block;margin:0;z-index: 0; height:auto;/*calc(100vh - 4.44rem)*/;min-width:auto;color:var(--color-dark);
background-color:none ;}

.Main .insights { 
    scroll-behavior: smooth;display: grid; gap:1rem;  padding: 1rem;
    max-height: fit-content;   border-radius: 20px;margin:4px;
    grid-template-columns: repeat(auto-fit, minmax(17.2rem,8rem));  }
.Main .insights::-webkit-scrollbar { width: 0 !important }
.Main .insights .project_item {
    background: var(--color-white);
    max-height: 250px;
    padding: 1rem;margin-top: .2rem;
    border-radius: var(--card-border-radius);    
    box-shadow: var(--box-shadow);
    transition: all .3s ease;
    /* max-width: fit-content;      */
}
    .Main .insights .project_item *{color:var(--color-dark)}
.Main .insights .project_item .circle{
    height: 5rem;   width: 5rem;    margin:1rem;
    border-radius:50%;  padding: .5rem 1rem;
    border:solid 1rem #7380ec;
    justify-content: space-around;}

.Main .insights > div:hover { box-shadow: none;} 
.Discussion{    
    min-width: 300px;
    background-color: var(--color-white);
    margin-right: .4rem;padding: 1rem;
    border-radius:var(--card-border-radius);
}
.inner_main_content{
    /* background-color: #41f1b6; */
    position:relative;
    display:grid;
    grid-template-columns:2fr 1fr;
    gap:0rem;
     padding: 1rem;
      height:88vh;
      
} 
/* style={{position:"relative",display:"grid",gridTemplateColumns:"2fr 1fr",gap:"2.5rem", padding:"0 1rem", height:"88vh"}} */

.login-back-container{z-index:151;position:absolute;display:none;height: 100%;width: 100%;place-items: center;background-color: rgba(0,0,0,0.41);}
#close_login:checked ~.login-back-container{display:grid;}


@media (max-width: 1080px) {
    .top{padding: 2px 50px;}
    .top .right-bar {width:32rem;gap:1rem;}
}
@media (max-width: 900px) {    
    .top .right-bar {width:28rem;}
    .top .right-bar .menu .menu-btn{padding:0rem  .4rem;}
}
@media (max-width: 800px) {
    .top{padding:2px 50px;}
    .top .right-bar{gap:.5rem}
    .top .right-bar .menu {width: 22rem;}
    .top .right-bar .menu .menu-btn{padding:0rem  .2rem;}
}
@media (max-width: 700px) {
    .inner_main_content{display:block; overflow:scroll}
    .top{padding:2px 50px;justify-content:space-between; overflow:visible;}
    .top .hamburger{display:block;}
    /**** Right Bar CSS Code Begin *****/
    .top .right-bar{   max-width:0px;overflow: hidden;
        display:block;  position: absolute;  z-index: 15;
        top:1.8rem;right:0; bottom:0;    min-height:100vh;
        background-color: var(--rightbar-color);  
        transition: all .3s ease; }
    .top .slided{max-width: 250px;padding:0 2rem;}
    .top .right-bar .menu{
        position: absolute; top:6rem;
        display: block;  max-height: 2.4rem;
        height:inherit;  width:250px;
        justify-content: space-around;}
        .top .right-bar .menu .menu-content {display:block;}
    .top .right-bar .UserBtn{display:block;position:absolute;top:1rem;height:3rem;
        background-color: #ffffff; width:3rem; padding:0 .75rem;}
    .sub-drop   {position:relative;left:10%;top:0;}
    /**** Right Bar CSS Code END *****/
    .top  .right-bar .menu .dropdown{position:relative;top:0;right:0;}
    /** Side left bar transformation **/
    .Main{height: 90vh;padding: 0;margin: 0;}
    .container{display: block;margin:0;height: 95vh;}
    .aside{min-width:100%;height: 35px; padding:0;margin: 0 ;}
    .aside .hamburger-right{display: none;}
    .aside .sidebar {width: 100%;display: flex; background:var(--color-white);padding:0px;margin: 0;transition: all .3s ease ;
        flex-direction: row;top:-6px; height: 35px; position: relative; overflow-x: scroll;overflow-y:visible;
    }
    .aside  ::-webkit-scrollbar{width: 0;}
    .aside .sidebar a{min-width: 38px;overflow: hidden; padding: 0; margin: 0;justify-content: center;}
    /* .aside .sidebar a span{font-size: medium;} */
    .aside .sidebar a.active{padding: 0; margin: 0;}
    .aside .sidebar a.active::before{content: none;}
    .aside .sidebar a h6{display: none;}
}
@media (max-width: 300px) {.top .right-bar{ width: 100%;}}

